<template>
    <div class="recommendMove">
        <goBack  :goBackName="goBackName" @goBackClick="goBackClick"  path='/'/>
        <searchList ref="searchList" @getGoBackName="getGoBackName"/>
    </div>
</template>

<script>
export default {
    data () {
        return {
            goBackName: ''
        }
    },
    components: {
        searchList: r => {
            require(['../components/searchList'],r)
        },
        goBack: r => {
            require(['../components/goBack'],r)
        }
    },
    watch: {
    },
    mounted () {
        if (this.$route.query.typeName) {
            switch (this.$route.query.typeName) {
                case '':
                    this.goBackName = ''
                    break;
                case '':
                    this.goBackName = ''
                    break;
                case '':
                    this.goBackName = ''
                    break;
                case '':
                    this.goBackName = ''
                    break;
                case '':
                    this.goBackName = ''
                    break;
                case '':
                    this.goBackName = ''
                    break;
                case '':
                    this.goBackName = ''
                    break;
                default:
                    this.goBackName = this.$route.query.typeName
            }
        }
    },
    activated () {
        if (this.$route.query.typeName) {
            switch (this.$route.query.typeName) {
                case '':
                    this.goBackName = ''
                    break;
                case '':
                    this.goBackName = ''
                    break;
                case '':
                    this.goBackName = ''
                    break;
                case '':
                    this.goBackName = ''
                    break;
                case '':
                    this.goBackName = ''
                    break;
                case '':
                    this.goBackName = ''
                    break;
                case '':
                    this.goBackName = ''
                    break;
                default:
                    this.goBackName = this.$route.query.typeName
            }
        } else {
            this.goBackName = ''
        }
    },
    methods: {
        goBackClick() {
           this.$refs.searchList.goBackInit()
        },
        getGoBackName (name) {
            this.goBackName = name
        }
    }
}
</script>

<style lang="scss">
    .recommendMove {
        .moveTitle {
            height: 1.28rem;
            line-height: 1.28rem;
            text-align: center;
            font-size: 0.48rem;
            color: #333;
            .goBack {
                float: left;
            }
        }
    }
</style>




